import React, { Component } from 'react';

class MsgContent extends Component {
  // ä¸ç¢ºå®šå¯ä»¥æ”¾å¤–é¢ã€é‚„æ˜¯è¦æ”¾åœ¨ constructor è£¡ï¼Ÿ
  msgContent = {
    addPost: {
      successMsg: 'æ–°å¢æ–‡ç« æˆåŠŸ',
      errorMsg: 'æ–°å¢æ–‡ç« å¤±æ•—',
    },
    deletePost: {
      successMsg: 'åˆªé™¤æ–‡ç« æˆåŠŸ',
      errorMsg: 'åˆªé™¤æ–‡ç« å¤±æ•—',
    },
    updatePost: {
      successMsg: 'ç·¨è¼¯æ–‡ç« æˆåŠŸ',
      errorMsg: 'ç·¨è¼¯æ–‡ç« å¤±æ•—',
    },
  }

  componentDidMount() {
    const { hideMsg } = this.props;
    this.timer = setTimeout(() => {
      hideMsg();
    }, 2000);
  }

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  render() {
    const { msg } = this.props;
    return (
      <div className='msg'>
        {
          msg.isSuccess ?
            this.msgContent[msg.type].successMsg :
            this.msgContent[msg.type].errorMsg
        }
      </div>
    )
  }
}

const Msg = ({ msg, hideMsg }) => (
  msg.isActive && <MsgContent msg={msg} hideMsg={hideMsg} />
)

export default Msg;